	module.exports = {"data":{"markdownRemark":{"html":"<h4>What are web components?</h4>\n<p>Compostable, reusable, customizable and semantic are the descriptors often used in documentation and various talks. A way to semantically build reusable code in html, css and javascript. The key it seems is the standards it uses to implement all the fancy descriptors.</p>\n<p>More than hype though, is the reality that if we can make the code have more use, we can worry less about getting the css or html right every time we want to achieve a certain look or functionality. We can import html, use custom elements, the shadow DOM, and a template element to tie it together.</p>\n<p>To me learning about web components goes nicely with ES6 and progressive web apps. Because for the first time, regardless of what framework is used, you can understand the web as a platform to creature unique experiences that are easily translated from one size screen to the next. This is all thanks to web components being standardized and following these specifications.</p>\n<blockquote>\n<h5>Specifications</h5>\n<p>Web components are based on four main specifications:</p>\n<p>Custom Elements</p>\n<p>The Custom Elements specification lays the foundation for designing >and using new types of DOM elements.</p>\n<p>Shadow DOM</p>\n<p>..The shadow DOM specification defines how to use encapsulated style >and markup in web components.</p>\n<p>HTML imports</p>\n<p>The HTML imports specification defines the inclusion and reuse of >HTML documents in other HTML documents.</p>\n<p>HTML Template</p>\n<p>The HTML template element specification defines how to declare >fragments of markup that go unused at page load, but can be >instantiated later on at runtime.\n<sup>1</sup></p>\n</blockquote>\n<h4>More Information:</h4>\n<h4>Sources</h4>\n<p>1,2. webcomponents.org <a href=\"https://www.webcomponents.org/introduction#libraries-for-building-web-components\">https://www.webcomponents.org/introduction#libraries-for-building-web-components</a></p>","fields":{"slug":"/web-components/"},"frontmatter":{"title":"Web Components"}}},"pathContext":{"slug":"/web-components/"}}

